@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Nunito", sans-serif;
  font-size: 25px;
}

div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
}

header {
  flex-basis: 40vh;
  position: relative;
  overflow: hidden;
}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 0;
}
aside {
  min-width: 240px;
}

section.blog {
  flex-grow: 1;
}

footer {
  text-align: center;
  color: rgb(9, 1, 39);
  background-color: darkolivegreen;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
  min-width: 100%;
  min-height: 100%;
}

.products h2 {
  margin-bottom: 20px;
  color: rgb(9, 1, 39);
}

.products ul {
  list-style: square;
  padding-left: 30px;
}

.products li {
  margin-bottom: 20px;
}

.products a {
  text-decoration: none;
  color: rgb(9, 1, 39);
}

.products a:hover {
  text-decoration: underline;
  color: yellowgreen;
}

.contact textarea {
  font-size: 16px;
  width: 80%;
  min-height: 200px;
  margin: 20px 0;
  line-height: 1.6;
}

.contact button {
  display: block;
  margin: 0 20% 0 auto;
  border: 2px solid black;
  background-color: #fff;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 20px;
}

.contact button:hover {
  color: yellowgreen;
  background-color: rgb(9, 1, 39);
}

label {
  display: block;
  margin: 10px 0;
}
input {
  margin-left: 10px;
}

nav {
  padding-left: 0 30px 30px 0;
}
ul {
  list-style-type: none;
}

nav.main a {
  display: block;
  text-decoration: none;
  color: rgb(9, 1, 39);
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px;
  transition: 0.2s;
}

nav.main a:hover {
  transform: translateX(20px);
}

nav.main a.active {
  transform: translateX(0);
}

footer span {
  color: greenyellow;
  text-transform: uppercase;
}

footer p {
  font-size: 15px;
  color: rgb(9, 1, 39);
}

